#practice-window{
    width: 95%;
    text-align: center;
    height: 36.5rem;
    background: #808080;
    /* justify-content: center; */
    border: 1px solid;
    position: relative;
    .header-section{
        display: inline-flex;
        width: 100%;
        padding-bottom: 0.5rem;
        .div-header-sec{
            width: 20%;
            background-color: cornsilk;
            border: black;
            border-radius: 10px;
            height: 2rem;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
    .div-content-section{
        /* display: inline-flex; */
        width: 100%;
        .records-info{
            width: 100%;
            display: flex;
            height: 100%;
            padding-bottom:0.2rem;
            .div-content-sec{
                width: 16.7%;
                background-color: azure;
                border: black;
                border-radius: 10px;
                font-size: 16px;
                display: flex;
                justify-content: center;
                align-items: center;
                .edit-btn{
                    background-image: url(../../../assets/img/edit.png);
                    background-repeat: no-repeat;
                    background-position: 100% center;
                    background-size: 14px;
                    width: 20px;
                    height: 20px;
                }
                .remove-btn{
                    background-image: url(../../../assets/img/remove.png);
                    background-repeat: no-repeat;
                    background-position: 100% center;
                    background-size: 14px;
                    width: 20px;
                    height: 20px;
                }
            }
        }
    }
    .footer-section{
        display: inline-flex;
        width: 100%;
        .footer-resize{
            width: 100%;
            display: inline-flex;
            position: absolute;
            .div-content-sec{
                width: 16.7%;
                background-color: azure;
                border: black;
                border-radius: 10px;
                font-size: 16px;
                display: flex;
                justify-content: center;
                align-items: center;
                .edit-btn{
                    background-image: url(../../../assets/img/edit.png);
                    background-repeat: no-repeat;
                    background-position: 100% center;
                    background-size: 14px;
                    width: 20px;
                    height: 20px;
                }
                .remove-btn{
                    background-image: url(../../../assets/img/remove.png);
                    background-repeat: no-repeat;
                    background-position: 100% center;
                    background-size: 14px;
                    width: 20px;
                    height: 20px;
                }
            }
        }
    }
}